/* Slider */
.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}

/*utility extend*/
/*Slick Slider*/
.slick-slider *{outline: none;}
.slick-slider:hover .slick-arrow{opacity: 0.75; transition: opacity 0.25s;transform: translateZ(0);will-change:transform;}
.slick-slider .slick-arrow{opacity:0;font-size: 0;border:0 none;background-color: transparent;cursor:pointer;padding:0;margin:0;width:100px;height:100px;z-index:1;position: absolute;top:50%;transform:translateY(-50%) translateZ(0);will-change: transform;transition: opacity 0.25s;}
.slick-slider .slick-arrow:hover{opacity:1;transition: opacity 0.25s;transform: translateZ(0);will-change:transform;}

.slick-slider .slick-arrow.slick-prev:before,
.slick-slider .slick-arrow.slick-prev:after,
.slick-slider .slick-arrow.slick-next:before,
.slick-slider .slick-arrow.slick-next:after{content:"";display:block;margin:auto;left:0;right:0;width:55px; height:3px;position: absolute;background-color: #ff617f;transition: transform 0.35s}

.slick-slider .slick-arrow.slick-prev{left:50px;}
.slick-slider .slick-arrow.slick-prev:before,
.slick-slider .slick-arrow.slick-prev:after{transform-origin: left center;}
.slick-slider .slick-arrow.slick-prev:before{transform:rotate(-45deg);top:49px;}
.slick-slider .slick-arrow.slick-prev:after{transform:rotate(45deg) translateZ(0);will-change: transform;bottom:49px;}
.slick-slider .slick-arrow.slick-prev:hover:before{transform:rotate(-60deg) translateZ(0);will-change: transform; transition: transform 0.35s }
.slick-slider .slick-arrow.slick-prev:hover:after{transform:rotate(60deg) translateZ(0);will-change: transform; transition: transform 0.35s }

.slick-slider .slick-arrow.slick-next{right:50px;}
.slick-slider .slick-arrow.slick-next:before,
.slick-slider .slick-arrow.slick-next:after{transform-origin: right center;}
.slick-slider .slick-arrow.slick-next:before{transform:rotate(45deg) translateZ(0);will-change: transform;top:49px;}
.slick-slider .slick-arrow.slick-next:after{transform:rotate(-45deg) translateZ(0);will-change: transform;bottom:49px;}
.slick-slider .slick-arrow.slick-next:hover:before{transform:rotate(60deg) translateZ(0);will-change: transform;transition: transform 0.35s}
.slick-slider .slick-arrow.slick-next:hover:after{transform:rotate(-60deg) translateZ(0);will-change: transform;transition: transform 0.35s}
/* END Slick Slider*/

@media screen and (max-width: 1440px) {
.slick-slider .slick-arrow.slick-prev{left: 0px;}
.slick-slider .slick-arrow.slick-next{right:0px;}
}

@media screen and (max-width: 1280px) {
.slick-slider .slick-arrow{ width:50px;height:50px;}
.slick-slider .slick-arrow.slick-prev:before, 
.slick-slider .slick-arrow.slick-prev:after, 
.slick-slider .slick-arrow.slick-next:before, 
.slick-slider .slick-arrow.slick-next:after{width:35px;}
.slick-slider .slick-arrow.slick-prev:before, 
.slick-slider .slick-arrow.slick-next:before{top:24px;}
.slick-slider .slick-arrow.slick-prev:after,
.slick-slider .slick-arrow.slick-next:after{bottom:24px;}
}

@media screen and (max-width: 960px) {
.slick-slider .slick-arrow{top:0;bottom:0;transform:none;height:auto;width: 75px;} 
.slick-slider .slick-arrow.slick-next{background: linear-gradient(90deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.35) 100%);}
.slick-slider .slick-arrow.slick-prev{background: linear-gradient(90deg, rgba(0,0,0,0.35) 0%, rgba(0,0,0,0) 100%);}
.slick-slider .slick-arrow.slick-next:before{top:50%;transform:translateY(-50%) rotate(45deg) translateZ(0);will-change: transform;margin-top:-1px}
.slick-slider .slick-arrow.slick-next:after{bottom:50%;transform:translateY(-50%) rotate(-45deg ) translateZ(0);will-change: transform;margin-bottom:-1px;}
.slick-slider .slick-arrow.slick-prev:before{top:50%;transform:translateY(-50%) rotate(-45deg) translateZ(0);will-change: transform;margin-top:-1px}
.slick-slider .slick-arrow.slick-prev:after{bottom:50%;transform:translateY(-50%) rotate(45deg ) translateZ(0);will-change: transform;margin-bottom:-1px;}

}
@media screen and (max-width: 769px) {
.slick-slider .slick-arrow{ width: 50px;} 
.slick-slider .slick-arrow.slick-next:before,
.slick-slider .slick-arrow.slick-prev:before{margin-top:-0.5px}
.slick-slider .slick-arrow.slick-prev:after,
.slick-slider .slick-arrow.slick-next:after{margin-bottom:-0.5px;}


  .slick-slider .slick-arrow.slick-prev:before, 
  .slick-slider .slick-arrow.slick-prev:after, 
  .slick-slider .slick-arrow.slick-next:before, 
  .slick-slider .slick-arrow.slick-next:after{width:15px;height:2px;}
}